.sidebar{
    font-family: 'OpenSans', sans-serif;
    .post-img {
        img {
            border: 4px solid #000000;
        }
    }
    &__date{
        color: #000000;
    }
    &__recent {
        h2 {
            font-family: 'VT323', monospace;
            color: #000000 !important;
            font-size: 2.5rem;
        }
        a {
            font-family: 'VT323', monospace;
            color: #2828ff !important;
            font-size: 1.5rem;
            &:hover {
                opacity: 0.7;
            }
        }
        a:before {
            content: '>';
            margin-right: 5px;
        }
    }
}