.card {
    background-color: #ffe095 !important;
    box-shadow: 0 0 2px rgba(40, 40, 255, 0.5);
    min-height: 520px;
    border: 1px solid #2828ff;
    border-radius: 0px;
    &:hover,
    &:focus,
    &:active {
      box-shadow: 0px 1px 0px #2828ff, 0px 2px 0px #2828ff, 0px 3px 0px #2828ff,
        0px 4px 0px #2828ff, 0px 6px 0px rgba(40, 40, 255, 0.1),
        0px 1px 0px rgba(40, 40, 255, 0.3), 0px 3px 0px rgba(40, 40, 255, 0.2),
        0px 5px 0px rgba(40, 40, 255, 0.25), 0 7px 7px rgba(40, 40, 255, 0.1),
        0 8px 7px rgba(40, 40, 255, 0.1), 0 9px 9px rgba(40, 40, 255, 0.1),
        0 10px 10px rgba(40, 40, 255, 0.15), 0 15px 15px rgba(40, 40, 255, 0.16);
    }
}

.card-body {
    background-color: #ffe095 !important;
    color: #000000 !important;
    font-family: 'OpenSans', sans-serif;
    h2.panel-title{
        font-family: 'VT323', monospace;
        color: #2828ff;
        font-size: 1.9rem;
    }
}

.pagination {
    font-family: 'VT323', monospace;
    margin: 0 1rem;
    font-size: 1.6rem;
    &--previous, &--next {
        color: #2828ff;
        outline: none;
        &:hover, &:focus, &:active {
            color: #2828ff;
            outline: none;
            text-decoration: none;
        }
    }
}

.disabled {
    color: #000000;
    &:hover {
        color: #000000 !important;
    }
}