.post-layout{
    margin-top: 150px;
    color: #000000;
    .post-title{
        font-family: 'VT323', monospace;
        color: #000000 !important;
        font-size: 4rem;
        text-decoration: none;
        line-height: normal;
    }
    .post-title::before {
        content: '> ';
    }
    .post-meta{
        time {
            color: #000000;
            padding: 5px;
            background-color: #ffe095;
        }

    }
    .post-content{
        font-family: 'OpenSans', sans-serif;
        font-size: 20px;
        a {
            text-decoration: underline;
        }
        .highlight {
            background-color: #ffe095;
            pre {
                padding: 20px 10px;
            }
        }
        &__pre {
            background-color: #ffe095;
            padding: 15px;
        }

        h1,h2 {
            margin: 30px 0px;
        }
        details  {
          padding: 10px;
          background-color: #ffe095;
          summary {
            padding: 10px;
            background-color: #2828ff;
            color: white;
          }
        }
        blockquote {
            border: none !important;
            background-color: #ffe095;
            border-left: 5px solid #2828ff !important;
            padding: 10px 10px 1px 10px;
            border-top-left-radius: 0px;
            border-bottom-left-radius: 0px;
            border-bottom-right-radius: 0px;
        }

        table{
            margin: 2% 0%;
            width: 100%;
            thead{
              tr{
                th{
                  padding: 5px;
                  border: 1px solid #000;
                  color: #000;
                  background-color: #ffe095;
                }
              }
            }
            tbody{
              background-color: #ffe095;
              tr{
                td{
                  padding: 5px;
                  border: 1px solid #000;
                  color: #000;
                  background-color: #ffe095;
                  code{
                    background-color: transparent;
                  }
                }
              }
            }
          }
    }
}