.navbar {
    background-color: #ffe095 !important;
    border-bottom-color: #2828ff;
    img {
        width: 4rem;
        height: 4rem;
    }
    &-brand {
        h1 {
            font-family: 'VT323', monospace;
            color: #2828ff;
            margin-left: 10px;
        }
    }
    .nav-link {
        font-size: 1.6rem;
        font-family: 'VT323', monospace;
        color: #2828ff !important;
        -moz-transition: all .2s ease-in;
        -o-transition: all .2s ease-in;
        -webkit-transition: all .2s ease-in;
        transition: all .2s ease-in;
        &:hover {
            opacity: 0.7;
        }
        .fa-magnifying-glass {
            font-size: 1rem;
        }
    }
    // .nav-link::after {
    //     content: '|';
    //     padding: 0rem;
    // }

    .navbar-nav .nav-item + .nav-item:before {
        content: '|';
        font-family: 'VT323', monospace;
        color: #000000 !important;
        font-size: 1.6rem;
        position: absolute;
        top: 1.5rem;
        margin-left: -0.3rem;
    }
}