.page-layout {
    .post-title{
        font-family: 'VT323', monospace;
        color: #000000 !important;
    }
    .post-title::before {
        content: '>';
        margin-right: 8px;
    }
    .post-meta{
        time {
            color: #000000;
            padding: 5px;
            background-color: #ffe095;
        }

    }
    .post-content {
        text-align: center;
        font-family: 'OpenSans', sans-serif;
        font-size: 20px;
        a {
            text-decoration: underline;
        }
        .highlight {
            background-color: #ffe095;
        }
    }

    .about_logotype{
        text-align: center;
        img {
            width: 400px;
            height: 400px;
        }
    }
}